"use client";

import { useEffect, useState, useContext } from "react";
import { AuthContext } from "../context/AuthContext";
import dynamic from "next/dynamic";

const Picker = dynamic(() => import("emoji-picker-react"), { ssr: false });

export default function PostsFeed() {
  const { user } = useContext(AuthContext);

  const [posts, setPosts] = useState([]);
  const [commentText, setCommentText] = useState({});
  const [replyText, setReplyText] = useState({});
  const [showEmoji, setShowEmoji] = useState({});
  const [replyInputVisible, setReplyInputVisible] = useState({});
  const [repliesVisible, setRepliesVisible] = useState({});
  const [lazyReplies, setLazyReplies] = useState({});

  const REPLIES_BATCH = 5;

  const fetchRandomPosts = async () => {
    try {
      const res = await fetch("/api/posts/random");
      const data = await res.json();
      setPosts(data.posts || []);
    } catch (err) {
      console.error("Fetch random posts error:", err);
    }
  };

  useEffect(() => {
    fetchRandomPosts();
  }, []);

  const handleLike = async (postId) => {
    if (!user) return alert("Please login first");

    await fetch(`/api/posts/${postId}/interact`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ type: "like" }),
    });

    fetchRandomPosts();
  };

  const handleComment = async (postId, parentId = null) => {
    if (!user) return alert("Please login first");

    const content = parentId ? replyText[parentId] : commentText[postId];
    if (!content || content.trim() === "") return;

    await fetch(`/api/posts/${postId}/interact`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ type: "comment", content, parentId }),
    });

    if (parentId) {
      setReplyText((prev) => ({ ...prev, [parentId]: "" }));
      setReplyInputVisible((prev) => ({ ...prev, [parentId]: false }));
    } else {
      setCommentText((prev) => ({ ...prev, [postId]: "" }));
    }

    fetchRandomPosts();
  };

  const handleCommentLike = async (commentId) => {
    if (!user) return alert("Please login first");

    await fetch(`/api/comments/${commentId}/like`, { method: "POST" });
    fetchRandomPosts();
  };

  const handleEmojiClick = (id, emojiData, isReply = false) => {
    if (isReply)
      setReplyText((prev) => ({
        ...prev,
        [id]: (prev[id] || "") + emojiData.emoji,
      }));
    else
      setCommentText((prev) => ({
        ...prev,
        [id]: (prev[id] || "") + emojiData.emoji,
      }));
  };

  const toggleReplies = (commentId) => {
    setRepliesVisible((prev) => ({ ...prev, [commentId]: !prev[commentId] }));

    if (!lazyReplies[commentId]) {
      setLazyReplies((prev) => ({
        ...prev,
        [commentId]: REPLIES_BATCH,
      }));
    }
  };

  const loadMoreReplies = (commentId) => {
    setLazyReplies((prev) => ({
      ...prev,
      [commentId]: Math.min(
        prev[commentId] + REPLIES_BATCH,
        posts
          .flatMap((p) => p.comments)
          .find((c) => c.id === commentId)?.replies?.length || 0
      ),
    }));
  };

  const renderReplies = (postId, replies, parentId) => {
    const countToShow = lazyReplies[parentId] || REPLIES_BATCH;

    return replies.slice(0, countToShow).map((reply) => (
      <li key={reply.id} style={{ marginLeft: "20px", marginTop: "5px" }}>
        <strong>{reply.author?.name || "Unknown"}:</strong> {reply.content}

        <button onClick={() => handleCommentLike(reply.id)} style={{ marginLeft: "10px" }}>
          ‚ù§Ô∏è {reply.likes?.length || 0}
        </button>

        {!replyInputVisible[reply.id] && (
          <button
            style={{ marginLeft: "10px" }}
            onClick={() =>
              setReplyInputVisible((prev) => ({
                ...prev,
                [reply.id]: true,
              }))
            }
          >
            Add Comment
          </button>
        )}

        {replyInputVisible[reply.id] && (
          <div style={{ marginTop: "5px" }}>
            <textarea
              placeholder="Write a reply..."
              value={replyText[reply.id] || ""}
              onChange={(e) =>
                setReplyText((prev) => ({
                  ...prev,
                  [reply.id]: e.target.value,
                }))
              }
              rows={1}
              style={{ width: "80%" }}
            />

            <button
              type="button"
              onClick={() =>
                setShowEmoji((prev) => ({
                  ...prev,
                  [reply.id]: !prev[reply.id],
                }))
              }
            >
              üòä
            </button>

            {showEmoji[reply.id] && (
              <Picker onEmojiClick={(e) => handleEmojiClick(reply.id, e, true)} />
            )}

            <button onClick={() => handleComment(postId, reply.id)}>Done</button>
          </div>
        )}

        {reply.replies?.length > 0 && (
          <>
            <button onClick={() => toggleReplies(reply.id)} style={{ marginTop: "5px" }}>
              {repliesVisible[reply.id]
                ? "Hide Replies"
                : `View Replies (${reply.replies.length})`}
            </button>

            {repliesVisible[reply.id] && (
              <>
                <ul>{renderReplies(postId, reply.replies, reply.id)}</ul>

                {lazyReplies[reply.id] < reply.replies.length && (
                  <button onClick={() => loadMoreReplies(reply.id)}>
                    View More Replies
                  </button>
                )}
              </>
            )}
          </>
        )}
      </li>
    ));
  };

  return (
    <div>
      {posts.length === 0 ? (
        <p>Loading posts...</p>
      ) : (
        <ul style={{ listStyle: "none", padding: 0 }}>
          {posts.map((post) => (
            <li
              key={post.id}
              style={{
                border: "1px solid #ccc",
                padding: "10px",
                marginBottom: "15px",
                borderRadius: "8px",
              }}
            >
              <strong>{post.author?.name || "Unknown"}</strong>

              <h3>{post.title}</h3>
              <p>{post.content}</p>

              {post.image && <img src={post.image} style={{ maxWidth: "50%" }} />}

              {post.video && (
                <video controls style={{ maxWidth: "50%" }}>
                  <source src={post.video} />
                </video>
              )}

              <small>{new Date(post.createdAt).toLocaleDateString()}</small>

              <div style={{ marginTop: "10px" }}>
                <button onClick={() => handleLike(post.id)}>Like</button>
                <span style={{ marginLeft: "10px" }}>{post._count?.likes || 0} Likes</span>
                <span style={{ marginLeft: "10px" }}>
                  {post._count?.comments || 0} Comments
                </span>
              </div>

              <div style={{ marginTop: "10px" }}>
                <textarea
                  placeholder="Write a comment..."
                  value={commentText[post.id] || ""}
                  onChange={(e) =>
                    setCommentText((prev) => ({
                      ...prev,
                      [post.id]: e.target.value,
                    }))
                  }
                  rows={2}
                  style={{ width: "100%" }}
                />

                <button
                  type="button"
                  onClick={() =>
                    setShowEmoji((prev) => ({
                      ...prev,
                      [post.id]: !prev[post.id],
                    }))
                  }
                >
                  üòä
                </button>

                {showEmoji[post.id] && (
                  <Picker onEmojiClick={(e) => handleEmojiClick(post.id, e)} />
                )}

                <button onClick={() => handleComment(post.id)}>Comment</button>
              </div>

              {post.comments?.length > 0 && (
                <ul style={{ marginTop: "10px" }}>
                  {post.comments.map((c) => (
                    <li key={c.id}>
                      <strong>{c.author?.name || "Unknown"}:</strong> {c.content}

                      <button
                        onClick={() => handleCommentLike(c.id)}
                        style={{ marginLeft: "10px" }}
                      >
                        ‚ù§Ô∏è {c.likes?.length || 0}
                      </button>

                      {!replyInputVisible[c.id] && (
                        <button
                          style={{ marginLeft: "10px" }}
                          onClick={() =>
                            setReplyInputVisible((prev) => ({
                              ...prev,
                              [c.id]: true,
                            }))
                          }
                        >
                          Add Comment
                        </button>
                      )}

                      {replyInputVisible[c.id] && (
                        <div style={{ marginTop: "5px" }}>
                          <textarea
                            placeholder="Write a reply..."
                            value={replyText[c.id] || ""}
                            onChange={(e) =>
                              setReplyText((prev) => ({
                                ...prev,
                                [c.id]: e.target.value,
                              }))
                            }
                            rows={1}
                            style={{ width: "80%" }}
                          />

                          <button
                            type="button"
                            onClick={() =>
                              setShowEmoji((prev) => ({
                                ...prev,
                                [c.id]: !prev[c.id],
                              }))
                            }
                          >
                            üòä
                          </button>

                          {showEmoji[c.id] && (
                            <Picker onEmojiClick={(e) => handleEmojiClick(c.id, e, true)} />
                          )}

                          <button onClick={() => handleComment(post.id, c.id)}>
                            Done
                          </button>
                        </div>
                      )}

                      {c.replies?.length > 0 && (
                        <>
                          <button
                            onClick={() => toggleReplies(c.id)}
                            style={{ marginTop: "5px" }}
                          >
                            {repliesVisible[c.id]
                              ? "Hide Replies"
                              : `View Replies (${c.replies.length})`}
                          </button>

                          {repliesVisible[c.id] && (
                            <>
                              <ul>{renderReplies(post.id, c.replies, c.id)}</ul>

                              {lazyReplies[c.id] < c.replies.length && (
                                <button onClick={() => loadMoreReplies(c.id)}>
                                  View More Replies
                                </button>
                              )}
                            </>
                          )}
                        </>
                      )}
                    </li>
                  ))}
                </ul>
              )}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
